document.addEventListener('DOMContentLoaded', () => {

  // ==========================================================================
  // 1. Скрипт для мобильного меню (Header)
  // ==========================================================================
  const menuToggle = document.getElementById('menuToggle');
  const headerNav = document.querySelector('.header__nav');
  const navLinks = document.querySelectorAll('.nav__link');

  const toggleMenu = () => {
      headerNav.classList.toggle('is-open');

      const iconElement = menuToggle.querySelector('svg');
      if (headerNav.classList.contains('is-open')) {
          iconElement.setAttribute('data-lucide', 'x');
      } else {
          iconElement.setAttribute('data-lucide', 'menu');
      }
      lucide.createIcons();
  };

  menuToggle.addEventListener('click', toggleMenu);

  navLinks.forEach(link => {
      link.addEventListener('click', () => {
          if (window.innerWidth < 768) {
              setTimeout(() => {
                  if (headerNav.classList.contains('is-open')) {
                      toggleMenu();
                  }
              }, 200);
          }
      });
  });


  // ==========================================================================
  // 2. Скрипт для Cookie Pop-up (Этап 5)
  // ==========================================================================
  const cookiePopup = document.getElementById('cookiePopup');
  const acceptCookiesButton = document.getElementById('acceptCookies');
  const cookieAccepted = localStorage.getItem('neurdata_cookies_accepted');

  const showCookiePopup = () => {
      if (!cookieAccepted) {
          cookiePopup.classList.remove('is-hidden');
      }
  }

  const hideCookiePopup = () => {
      cookiePopup.classList.add('is-hidden');
      localStorage.setItem('neurdata_cookies_accepted', 'true');
  }

  showCookiePopup();
  acceptCookiesButton.addEventListener('click', hideCookiePopup);


  // ==========================================================================
  // 3. JS Mouse Glow Follower (Будет добавлен)
  // ==========================================================================
  const mouseGlow = document.getElementById('mouse-spotlight');

  if (mouseGlow && window.innerWidth > 768) {
      document.addEventListener('mousemove', (event) => {
          requestAnimationFrame(() => {
              mouseGlow.style.transform = `translate(${event.clientX}px, ${event.clientY}px)`;
          });
      });
  }

  // ==========================================================================
  // Сюда будет добавляться логика форм и анимаций секций
  // ==========================================================================
});
// ==========================================================================
    // 4. JS Анимация Hero-секции (Data Stream / Matrix Effect)
    // ==========================================================================
    const canvas = document.getElementById('matrixCanvas');

    if (canvas) {
        const ctx = canvas.getContext('2d');
        let W = canvas.width = window.innerWidth;
        let H = canvas.height = window.innerHeight;
        const font_size = 18;
        let columns = Math.floor(W / font_size);
        const drops = [];
        const chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789$+-*/=";

        for (let i = 0; i < columns; i++) {
            drops[i] = 1;
        }

        const neonColor = "rgba(57, 255, 20, 0.9)";
        const trailColor = "rgba(0, 0, 0, 0.05)";

        function drawMatrix() {
            ctx.fillStyle = trailColor;
            ctx.fillRect(0, 0, W, H);

            ctx.font = `${font_size}px 'Roboto Mono'`;

            for (let i = 0; i < drops.length; i++) {
                const text = chars[Math.floor(Math.random() * chars.length)];
                const x = i * font_size;
                let y = drops[i] * font_size;

                ctx.fillStyle = neonColor;
                ctx.shadowColor = neonColor;
                ctx.shadowBlur = 8;

                ctx.fillText(text, x, y);

                ctx.shadowBlur = 0;

                if (y > H && Math.random() > 0.975) {
                    drops[i] = 0;
                }

                drops[i]++;
            }
        }

        setInterval(drawMatrix, 50);

        window.addEventListener('resize', () => {
            W = canvas.width = window.innerWidth;
            H = canvas.height = window.innerHeight;
            columns = Math.floor(W / font_size);
            drops.length = columns;
        });
    }
    // ==========================================================================
    // 4. Дополнительные стили для анимации (Требуется для Method, Consult, Results)
    // Добавьте этот блок перед существующей логикой Intersection Observer (где-то выше него)
    // ==========================================================================

    // Скрываем элементы и задаем смещение
    const animateStyles = document.createElement('style');
    animateStyles.textContent = `
        .content-animate {
            opacity: 0;
            transition: opacity 1s ease-out, transform 1s ease-out;
        }
        .content-animate[data-animation="left"] {
            transform: translateX(-100px);
        }
        .content-animate[data-animation="right"] {
            transform: translateX(100px);
        }
        .content-animate[data-animation="top"] {
            transform: translateY(-50px);
        }
        .content-animate[data-animation="bottom"] {
            transform: translateY(50px);
        }
        /* Финальное состояние */
        .content-animate.is-visible {
            opacity: 1;
            transform: translateX(0) translateY(0);
        }
    `;
    document.head.appendChild(animateStyles);

    // ... (Остальной код, включая логику Cookie Pop-up и Mouse Glow) ...

    // ==========================================================================
    // 5. Intersection Observer для активации анимации
    // ==========================================================================
    const animatedElements = document.querySelectorAll('.content-animate');

    if (animatedElements.length > 0) {
        // ... (используйте существующий Intersection Observer для всех .content-animate)
        const observerOptions = {
            root: null,
            rootMargin: '0px',
            threshold: 0.1
        };

        const observerCallback = (entries, observer) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.classList.add('is-visible');
                    observer.unobserve(entry.target);
                }
            });
        };

        const contentObserver = new IntersectionObserver(observerCallback, observerOptions);

        animatedElements.forEach(el => {
            contentObserver.observe(el);
        });
    }

    // ... (конец DOMContentLoaded)

    // ==========================================================================
    // 8. JS Логика Формы Контактов и CAPTCHA (Этап 4)
    // ==========================================================================
    const contactForm = document.getElementById('contactForm');
    const captchaDisplay = document.getElementById('captchaDisplay');
    const captchaInput = document.getElementById('captchaInput');
    const captchaMessage = document.getElementById('captchaMessage');
    const submissionMessage = document.getElementById('submissionMessage');
    const policyAccept = document.getElementById('policyAccept');

    let correctAnswer = 0;

    /**
     * Генерирует простой математический пример (CAPTCHA).
     */
    function generateCaptcha() {
        const operator = Math.random() < 0.5 ? '+' : '-';
        let num1 = Math.floor(Math.random() * 15) + 5;
        let num2 = Math.floor(Math.random() * 10) + 1;

        if (operator === '-' && num1 < num2) {
            [num1, num2] = [num2, num1];
        }

        correctAnswer = operator === '+' ? num1 + num2 : num1 - num2;
        captchaDisplay.textContent = `${num1} ${operator} ${num2} = ?`;
        captchaMessage.textContent = '';
        captchaInput.value = '';
    }

    /**
     * Валидирует ответ CAPTCHA.
     * @returns {boolean} True, если ответ верный.
     */
    function validateCaptcha() {
        if (!captchaInput.value.trim()) {
            captchaMessage.textContent = 'Пожалуйста, решите пример.';
            captchaMessage.style.color = '#FF4545'; // Красный
            return false;
        }

        const userAnswer = parseInt(captchaInput.value.trim());
        if (userAnswer === correctAnswer) {
            captchaMessage.textContent = 'Капча успешно пройдена!';
            captchaMessage.style.color = 'var(--color-primary)';
            return true;
        } else {
            captchaMessage.textContent = 'Неверный ответ. Попробуйте еще раз.';
            captchaMessage.style.color = '#FF4545';
            generateCaptcha();
            return false;
        }
    }

    // Инициализация CAPTCHA при загрузке страницы
    generateCaptcha();

    // Обработчик отправки формы
    contactForm.addEventListener('submit', function(event) {
        event.preventDefault();
        submissionMessage.style.display = 'none';

        const isCaptchaValid = validateCaptcha();
        const isPolicyAccepted = policyAccept.checked;

        if (isCaptchaValid && isPolicyAccepted) {

            // Имитация успешной отправки данных
            console.log('Form Submitted and Validated:', {
                name: document.getElementById('contactName').value,
                email: document.getElementById('contactEmail').value,
                phone: document.getElementById('contactPhone').value,
                policy: isPolicyAccepted
            });

            // Показываем сообщение об успехе ТОЛЬКО после успешной валидации
            submissionMessage.style.display = 'block';

            // Сброс формы и генерация новой капчи
            contactForm.reset();
            generateCaptcha();

            // Автоматически скрываем сообщение через 5 секунд
            setTimeout(() => {
                submissionMessage.style.display = 'none';
            }, 5000);

        } else if (!isPolicyAccepted) {
            alert('Пожалуйста, примите условия использования и политику конфиденциальности.');
            policyAccept.focus();
        }
    });